import React from 'react';
import arrow from '../../assets/iconsBanners/arrowStatic.svg'
import s from './Statistics.module.css';

const Statistics = (props) => {
   return (
      <div className={`borderMain ${s.bannerContainer}`}>
         <div className={s.title}>
            <img src={arrow} alt="" /> 30 185 
         </div>
         <div className={s.textBody}>
            <div>C  </div>
            <div> </div>
         </div>

         <div className={`mt32 ${s.title}`}>
            25,37 <sup>2</sup>
         </div>
         <div className={s.textBody}>
            <div>   </div>
            <div></div>
         </div>
         <div className="mt32">
            <div className={s.rowContainer}>
               <div className={s.tags}>25 </div>
               <div className={s.tags}>100  </div>
            </div>
            <div className={s.tags}>40  </div>
            <div className={s.tags}>20  </div>
         </div>

      </div>
   )
}
export default Statistics;