import React from 'react';
import s from './UsefulSourse.module.css';
/*   */
import banner from '../../assets/img/banners/contacts.png';
/*  */
import vk from '../../assets/icons/vk.svg';
import telegram from '../../assets/icons/telegram.svg';
import ok from '../../assets/icons/ok.svg';

const UsefulSourse = (props) => {
   return (
      <div className='mt80'>
         <div className='columnContainer'>
            <div className="columnLarge">
               <div className="borderMain">
                  <div className={s.title}> </div>
                  <div className={s.itemTextContainer}>
                     <div className={s.column}>
                        <div className={s.item}>   </div>
                        <div className={s.item}>    </div>
                        <div className={s.item}>   </div>
                        <div className={s.item}>- </div>
                        <div className={s.item}>       </div>
                        <div className={s.item}>      </div>
                        <div className={s.item}></div>
                     </div>
                     <div className={s.column}>
                        <div className={s.item}>    ( 01.01.2023 -     )</div>
                        <div className={s.item}>      </div>
                        <div className={s.item}>      ( 10.01.2022 -   )</div>
                        <div className={s.item}>    </div>
                        <div className={s.item}>    </div>
                        <div className={s.item}>      </div>
                     </div>
                     <div className={s.column}>
                        <div className={s.item}>  </div>
                        <div className={s.item}> </div>
                        <div className={s.item}>- </div>
                        <div className={s.item}> </div>
                        <div className={s.item}> </div>
                        <div className={s.item}></div>
                        <div className={s.item}>     </div>
                     </div>
                  </div>
               </div>
            </div>
            <div className="ml20 columnSmal">
               <div className={`borderMain ${s.bannerContainer}`}>
                  <div>
                     <div className={s.titleBanner}></div>
                     <div className={`mt24 ${s.tel}`}>+7 (495) 572 65 00</div>
                     <div className={s.subTitleBanner}>  </div>
                     <div className={`mt16 ${s.tel}`}>+7 (495) 793 01 01</div>
                     <div className={s.subTitleBanner}>  </div>
                     <div className={s.socialMediaContainer}>
                        <div className={s.socialMedia}><img src={vk} alt="" /></div>
                        <div className={s.socialMedia}><img src={telegram} alt="" /></div>
                        <div className={s.socialMedia}><img src={ok} alt="" /></div>
                     </div>
                  </div>
                  <div className={`btnRad ${s.btn}`}> </div>
               </div>
            </div>
         </div>
         <div className={s.linkContainer}>
            <a href="#" className={s.link}> </a>
            <a href="#" className={s.link}>  </a>
         </div>
      </div>
   )
}
export default UsefulSourse;