import React, { useState } from 'react';
import News from '../../Components/News';
import ContantContainerMain from '../../total/ContantContainerMain';
import Calendar from '../../total/Calendar';
import ReactSelect from '../../total/ReactSelect';
import s from './NewsPage.module.css';

/*  */
import news from '../../backend/news/news';
import EventAnnouncements from '../../Components/EventAnnouncements';
import UsefulSourse from '../../Components/UsefulSourse';
import VkChannel from '../../BannersComopnents/VkChannel';
import TgChannel from '../../BannersComopnents/TgChannel';

const NewsPage = (props) => {

   //ReactSelect
   // 
   const filterRelevanceData = [
      { id: '01', value: 'new', text: ' ' },
      { id: '02', value: 'currentMonth', text: '  ' },
      { id: '03', value: 'monthAgo', text: ' ' },
   ]


   const [filterRelevance, setFilterRelevance] = useState(filterRelevanceData[0]);

   const ternaryArrNews = [...news, ...news, ...news]


   const filterThemeData = [
      { id: 1, value: 'all', text: ' ' },
      { id: 2, value: '', text: '' },
      { id: 3, value: '', text: '' },
      { id: 4, value: '', text: '' },
      { id: 5, value: '', text: '' },
      { id: 6, value: '', text: '' },
      { id: 7, value: '', text: '' },
      { id: 8, value: '', text: '' },
      { id: 9, value: '', text: '' },
      { id: 10, value: '', text: '' },
      { id: 11, value: '', text: '' },
      { id: 12, value: '', text: '' },
      { id: 13, value: '', text: '' },
      { id: 14, value: '', text: '' },
   ]

   const [filterTheme, setFilterTheme] = useState(filterThemeData[0]);
   return (
      <div>
         <ContantContainerMain>
            <h1 className="mt80 pageTitle"></h1>
            <div className="mt80 columnContainer">

               <div className="mr20 columnSmal">
                  <div className="filter">

                     <ReactSelect
                        name='filterRelevance'
                        valuesOptions={filterRelevanceData}//    
                        isMulti={false}// 
                        placeholder={filterRelevance.text}
                        initialValue={filterRelevance}///   .  isMulti true,   !
                        onChangeValue={setFilterRelevance}//,   
                        labelName={'text'}//    label ( )
                        valueName={'value'}//    value (  )
                        isSearchable={false}//   
                     />

                     <div className="mt20">
                        <ReactSelect
                           name='filterTheme'
                           valuesOptions={filterThemeData}//    
                           isMulti={false}// 
                           placeholder={filterTheme.text}
                           initialValue={filterTheme}///   .  isMulti true,   !
                           onChangeValue={setFilterTheme}//,   
                           labelName={'text'}//    label ( )
                           valueName={'value'}//    value (  )
                           isSearchable={false}//   
                        />
                     </div>

                     <div className="mt20 filterThemeContainer">
                        <input type="text" value={'24.06.2024 - 30.06.2024'} onChange={() => false} className="inputCalendar" />
                        <Calendar />
                     </div>

                     <div className="mt20 filterSearchContainer"><input type="text" placeholder=' ' className="filterSearch" /></div>
                     <button className='mt20 filterBtnNewsArchive'> </button>
                     <div className="mt12 juridicalInfo">     22.04.2024    77 - 87145</div>

                  </div>
                  <div className='mt40'>
                     <VkChannel />
                  </div>
                  <div className='mt40'>
                     <TgChannel />
                  </div>
               </div>
               <div className="columnLarge">
                  <News news={ternaryArrNews} />
               </div>

            </div>
            <EventAnnouncements />

            <UsefulSourse />

         </ContantContainerMain>
      </div>
   )
}
export default NewsPage;