import React from 'react';
import ContantContainerMain from '../../total/ContantContainerMain';
import logo from '../../assets/img/logos/full-logo.svg';
import s from './Footer.module.css';

const Footer = (props) => {
   return (
      <div className={s.footer}>
         <ContantContainerMain>
            <div className={s.footerRow}>
               <div className={s.footerLogoBlock}>
                  <img src={logo} alt="" />
                  <div className={s.textMain}>
                     <div className={s.titleInfo}> -</div>
                     <div className={s.titleAdministration}>   </div>
                     <div className={s.titleInfo}> </div>
                  </div>
               </div>
               <div className={s.footerColumn}>
                 <div className={s.copyright}>2024        </div>
                 <div className={s.copyright}>        admhimki.ru</div>
                 <div className={s.adress}> ., .. , . , .15, . , . 4</div>
                 <div className={s.tsMedia}> - TS Media</div>
               </div>
            </div>
         </ContantContainerMain>
      </div>
   )
}
export default Footer;